.category>p{
    text-align: center;
    margin: 20px;
    font-weight: 600;
    color: rgb(23, 26, 240);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 2rem;
}

.categoryData{
    display: flex;
    text-align: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.imageText{
    color:black;
    cursor:'pointer';
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-decoration: none;
    
}
.imageText:hover{
    color: red;
    text-decoration: none;
}


.firstheading{
text-align: center;
margin-top: 4rem;


}
.firstheading:first-child{
    font-size: 2rem ;
    color: rgba(32, 34, 36, 0.747);
}
.sellheading{
    font-size: 3.5rem ;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
   color: black;
}


.banner>p{
    font: 300 1.4vmax "Lucida Sans";
}
.banner>a>button{
    margin-bottom: 5vmax;
    cursor: pointer;
    background-color: white;
    border: 1px solid white;
    
    border-radius: 0;
    padding: 1vmax;
    transition: all  .5s;
    width: 9vmax;
    font: 500 1vmax "Roboto";
}
.banner>a>button:hover{
    background-color: rgb(45, 43, 59);
    color: white;
}
.banner::after{
    content: " ";
    width: 100vw;
    height: 100vmin;
    background-color: white;
    position: absolute;
    top: 0%;
    left: 0;
    clip-path: polygon(100% 71%, 0% 100%, 100% 100%);
    width: 100%;

}


  @media (max-width: 767px) {
    
    .typing-text {
      font-size: 8vw;
    }
  }

