.productpricespan{
    margin: 0.5vamx .5vmax;
    color: rgb(10, 10, 10);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 1.3rem;
    padding-left: 1rem;
    padding-bottom: 1rem;
}

.productCard:hover{
    box-shadow: 0 0 5px rgba(15,15,15,.25);
    border-radius: 5px;
    padding: 1px;
    transform: translateY(-1vmax);
    border-bottom: 2px solid red;
    
}

.closeBtnBox{
    display: flex;
    justify-content: space-between;
}
.closeBtnBox>button{
    background-color: rgb(238, 113, 113);
    width: 10px;
}
.dialogcontent{
    width: 32vw;
    height: 36vh !important;
}

.closeBtnBox>button:hover{
    background-color: rgb(236, 69, 69);
    transition: all .5s;
}

@media screen and (max-width:900px){
    .productcardname {
        font-size: 1.2rem !important;}
    .productCard>p{
        font-size: 1.7vmax;
    }
   
    .productCard>div{
        margin: 0vmax;
        display: block;

    }
    .productCard>span{
        font-size: 1.5vmax;

    }
    .rating{
        font-size: 2vmax !important;
    }
    .productCard>div>span{
        margin: 0 0.5vmax;
        font:300 1vmax "Roboto"
    }
    .productpricespan{
        font-size: 1.2rem;
    }
    
}

.homepageImage{
    display: none;
}

@media screen and (max-width:413px){
    .productCard>p{
        font-size: 4rem;
    }
    .imgcard{
    
        width:15rem;
        height:15rem;
    }
    .productCard>div{
        margin: 0vmax;
        display: block;

    }
    .productcardname {
        font-size: 2vmax !important;
        margin: 8px auto;
        margin-bottom: 5px;
    }
    .rating{
        font-size: 2.5vmax !important;
    }
    .productpricespan{
        font-size: 2vmax;
    }
    
}
@media screen and (max-width:1000px) {
    .rightImage{
        display: none;
    }
    
    .leftImage{
        display: none;
    }
}